import React from 'react'
import '../App.css';
import {}from 'react-router-dom'

function Basket(props) {
    const {carditem,onadd,onremove} = props;
    const itemsprice=carditem.reduce((a,c)=>a+c.price *c.qyt,0);
    const taxprice=itemsprice * 0.14;
    const shippingprice=itemsprice>2000?  50:0 ;
    const totalprice=itemsprice+taxprice+shippingprice;
    return (
        <div className='col-sm-4 basket'>
            <div className='mt-3'>
           <h3>basket</h3> 
           <div>
           {carditem.length===0 && <div> Basket is empty </div>}
           </div>
      {carditem.map((item)=>(
           <div key={item.id} className='row mt-4'>
               <div className=''>{item.name}</div><span>&nbsp;&nbsp;</span>
           <button onClick={()=>onadd(item)} className='btn btn-success ml-2 mr-2'>+</button><span>&nbsp;</span>
           <button onClick={()=>onremove(item)}  className='btn btn-danger mr-2'>-</button>
           
           <div>{item.qyt} * ${item.price.toFixed(2)}</div>
          </div> ))}
</div>
          {carditem.length!==0 && (
              <>
              <hr/>
              <div className='row'>
                
              <div className='d-flex flex-column text-left'> 
<ul>
             <li><p> items price:  ${itemsprice.toFixed(2)}</p></li> 
              
                  

                 
             <li> <p> tax price:   ${taxprice.toFixed(2)}</p></li>
            
             <li> <p> shipping price:  ${shippingprice.toFixed(2)}</p></li>
              
            <li>   <p>  total price  <strong>${totalprice.toFixed(2)}</strong> </p></li>
</ul>
               </div>
              </div>
              </>
          )}
          <button 
         className='btn btn-success w-50 mb-1 '> {totalprice}</button>
        </div>
    )
}

export default Basket
